<template>
  <div id="appointment-modifier" class="container mt-5">
    <!-- Imagen de cabecera (si deseas mantenerla) -->
    <div class="header-image">
      <img :src="require('@/assets/logo-kizuna-sticky-150x150.png')" alt="CITAGEST">
    </div>

    <h1 class="mb-4 text-center">Modificar Cita</h1>
    <form @submit.prevent="updateAppointment">
      <div class="form-group">
        <label for="input-nombre">Nombre:</label>
        <input type="text" class="form-control" id="input-nombre" v-model="appointmentData.nombre" required>
      </div>
      
      <div class="form-group">
        <label for="input-email">Correo electrónico:</label>
        <input type="email" class="form-control" id="input-email" v-model="appointmentData.email" required>
      </div>
      
      <div class="form-group">
        <label for="input-date">Fecha:</label>
        <input type="date" class="form-control" id="input-date" v-model="appointmentData.fecha" required>
      </div>
      
      <div class="form-group">
        <label for="input-time">Hora:</label>
        <input type="time" class="form-control" id="input-time" v-model="appointmentData.hora" required>
      </div>

      <button type="submit" class="btn btn-primary btn-block">Actualizar Cita</button>
    </form>
  </div>
</template>

  
<script>
  import axios from 'axios';
  
  export default {
    name: 'ModifyAppointment',
    data() {
      return {
        appointmentData: {
          id: null,
          nombre: '',
          email: '',
          fecha: '',
          hora: ''
        }
      };
    },
    mounted() {
      this.appointmentId = this.$route.params.id;
      this.fetchAppointmentData();
    },
    methods: {
      async fetchAppointmentData() {
        try {
          const response = await fetch(`/api/get_appointment.php?appointment_id=${this.appointmentId}`);
          const data = await response.json();
          console.log(data); // Agrega esto para depurar
          if (data.success) {
            //alert(this.appointmentData)
            this.appointmentData = data.appointment;
          } else {
            alert(data.message || 'Error al cargar los datos de la cita');
          }
        } catch (error) {
          console.error('Error:', error);
          alert('Hubo un error al cargar los datos de la cita');
        }
      },
      updateAppointment() {
        console.log('Datos de la cita a actualizar:', this.appointmentData);

        axios.post('/api/modify_appointment.php', this.appointmentData)
          .then(response => {
            // Manejo de la respuesta del servidor
            console.log(response.data);
            // Muestra un mensaje de éxito al usuario
            alert("La cita ha sido actualizada exitosamente.");

            // Redireccionar al usuario a la página de inicio o a la lista de citas
            // Asegúrate de que tienes una ruta definida como '/' en tu vue-router
            this.$router.push('/');
          })
          .catch(error => {
            // Manejo del error
            console.error('Hubo un error al actualizar la cita:', error);
            // Muestra un mensaje de error al usuario
            alert("Hubo un error al actualizar la cita.");
          });
      },
    },


    // Puedes usar el hook created o mounted para cargar la cita existente si es necesario.
    // created() {
    //   this.loadAppointmentData();
    // }
  }
  </script>
  
  <style>
  .header-image img {
    max-width: 150px;  /* Establece el ancho máximo al tamaño original */
    max-height: 150px; /* Establece el alto máximo al tamaño original */
    display: block;    /* Evita que la imagen sea inline */
    margin: 0 auto;    /* Centra la imagen horizontalmente */
    height: auto;      /* Asegura que la altura sea automática para mantener la proporción */
  }


  .appointment-form {
    max-width: 400px;
    margin: 0 auto;
    padding: 1em;
    border: 1px solid #ccc;
    border-radius: 1em;
    background-color: #f9f9f9; /* Color de fondo suave */
  }
  .input-group {
    margin-bottom: 1em;
  }
  label {
    display: block;
    margin-bottom: .5em;
  }
  .form-control {
    width: 100%;
    padding: .5em;
    border: 1px solid #ccc;
    border-radius: .25em;
    background-color: #fff; /* Fondo blanco para los campos de entrada */
  }
  .submit-button {
    padding: .5em 1em;
    color: white;
    background-color: #28a745; /* Color verde para el botón */
    border: none;
    border-radius: .25em;
    cursor: pointer;
  }
  .submit-button:hover {
    background-color: #218838; /* Color verde más oscuro al pasar el mouse */
  }
</style>