import { createRouter, createWebHashHistory } from 'vue-router';
import ListaCitas from '@/components/ListaCitas.vue';
import CalendarioReservas from '@/components/CalendarioReservas.vue';
import ModifyAppointment from '@/components/ModifyAppointment.vue';

const routes = [
  {
    path: '/',
    name: 'ListaCitas',
    component: ListaCitas
  }, 
  {
    path: '/calendario',
    name: 'CalendarioReservas',
    component: CalendarioReservas
  },
    {
      path: '/modify-appointment/:id',
      name: 'ModifyAppointment',
      component: ModifyAppointment
    },

  // ... otras rutas
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;



