<template>
      <div class="header-image">
      <img :src="require('@/assets/logo-kizuna-sticky-150x150.png')" alt="CITAGEST">
    </div>
  <div id="app" class="container mt-5">
    <h1 class="mb-4 text-center">Registro de citas</h1>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="name">Nombre:</label>
        <input type="text" class="form-control" id="name" v-model="appointment.name" required>
      </div>
      <div class="form-group">
        <label for="email">Correo electrónico:</label>
        <input type="email" class="form-control" id="email" v-model="appointment.email" required>
      </div>
      
      <div class="form-group">
        <label for="date">Fecha:</label>
        <input type="date" class="form-control" id="date" v-model="appointment.date" required>
      </div>
      
      <div class="form-group">
        <label for="time">Hora:</label>
        <input type="time" class="form-control" id="time" v-model="appointment.time" required>
      </div>      
      <button type="submit" class="btn btn-primary btn-block">Registrar Cita</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "AppointmentForm",
  data() {
    return {
      appointment: {
        name: '',
        email: '',
        date: '',
        time: ''
      }
    }
  },
  methods: {
    async submitForm() {
      try {
        const response = await fetch('/api/register_appointment.php', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
          },
          body: new URLSearchParams(this.appointment)
        });

        const result = await response.text();
        console.log(result);
        alert("Cita registrada con éxito");
        this.$router.push('/');
      } catch (error) {
        console.error('Error al registrar la cita:', error);
        alert("Hubo un error al registrar la cita");
      }
    }
  }
}
</script>

<style scoped>
  .header-image img {
    max-width: 150px;  /* Establece el ancho máximo al tamaño original */
    max-height: 150px; /* Establece el alto máximo al tamaño original */
    display: block;    /* Evita que la imagen sea inline */
    margin: 0 auto;    /* Centra la imagen horizontalmente */
    height: auto;      /* Asegura que la altura sea automática para mantener la proporción */
  }
form {
  max-width: 400px;
  margin: 0 auto;
  padding: 1em;
  border: 1px solid #ccc;
  border-radius: 1em;
}
.form-group {
  margin-bottom: 1em;
}
label {
  display: block;
  margin-bottom: .5em;
}
input {
  width: 100%;
  padding: .5em;
  border: 1px solid #ccc;
  border-radius: .25em;
}
button {
  padding: .5em 1em;
  color: white;
  background-color: #007BFF;
  border: none;
  border-radius: .25em;
  cursor: pointer;
}
button:hover {
  background-color: #0056b3;
}
</style>